import Vue from 'vue'
import VueRouter from 'vue-router'
// import HomePage from '../views/HomePage.vue'
import Navbar from '../views/Navbar'
// import axios from 'axios';

const Device = () => import("@/views/Device.vue")
const DashboardDevice = () => import("@/views/DashboardDevice.vue")
const login = () => import("@/views/Login.vue")
const Electric = () => import("../components/Chart/ElectricBill.vue")

Vue.use(VueRouter)

const routes = [
  { path: '/login',name: "login", component: login },
  {
    path: "/",
    name: "Navbar",
    component: Navbar,
    children: [
      {
        path: "/DashboardDevice",
        name: "DashboardDevice",
        component: DashboardDevice
      },
      {
        path: "/Device",
        name: "Device",
        component: Device
      },
      {
        path: "/ElectricBill",
        name: "ElectricBill",
        component: Electric
      },
  { path: '/*', redirect: '/login' },
    ]
  }
]
const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
});

export default router;

 


